@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
 
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    margin: 0;
    padding: 0;
    background-color: #eae9fc;
    font-family: 'Poppins';
}


.wrapper{
    height: 100vh;
   
    display:flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    margin: auto;
}

.heroImg{
    
    height: 550px;
    position: absolute;
    bottom: 0;
    right:5%;
}
.text{
    
    font-size: 1.9rem;
    color: #8379ec;
    font-family: "Poppins";
    width: 50%;
    
}
.btns{
    width: 50%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.css-button-rounded--rose {
    min-width: 130px;
    height: 40px;
    color: #fff;
    padding: 10px 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    outline: none;
    border-radius: 5px;
    border: 2px solid #df2070;
    background: #df2070;
    font-size: 1rem;
  }
  .css-button-rounded--rose:hover {
    background: #0a062d;
    color: #df2070;
  }

  @media screen and (max-width: 480px) {
    .wrapper{
        height: 100vh;
        flex-direction: column;
        justify-content: center;
        align-items: center;
         
    }
     
     .text{
         font-size: 1.2rem;
         text-align: center;
         width: 100%;
     }
     .btns{
        width: 100%;
    }
    
      
  }
  
  @media screen and (max-width: 890px) {
    .heroImg{
        display: none;
    }
  }
.sem-wrapper{
    width: 90%;
    margin: auto;
    display: flex;
    flex-direction: column;
    
}
.heading{
    color: #8379ec;
    font-size: 2.3rem;
}
.container{
    height: 100vh;
    
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    grid-gap:25px;
    gap:25px;
    
}

.card{
    height: 300px;
     
    background-color: #d6d2f9;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.014), 0 6.7px 5.3px rgba(0, 0, 0, 0.02),
		0 12.5px 10px rgba(0, 0, 0, 0.025), 0 22.3px 17.9px rgba(0, 0, 0, 0.03),
		0 41.8px 33.4px rgba(0, 0, 0, 0.036), 0 100px 80px rgba(0, 0, 0, 0.05);
        transition: all .2s ease-in-out;
}
.card:hover{

   
    -webkit-transform: scale(1.1);

   
            transform: scale(1.1);
     
}
 
#sub{
    color: #df2070;
}

#lk{
    text-decoration: none;
}
 

 
