  width: 60%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, auto));
  grid-gap: 25px;
  gap: 25px;
}
.cnt2 {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#note {
  font-style: italic;
  font-weight: 100;
  color: #df1f70;
}
