.result {
  width: 50%;
  margin: 30px;
  background-color: #d6d2f9;
  border-radius: 12px;
  padding-left: 50px;
  color: #594de5;
}
.bt {
  width: 60%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, auto));
  grid-gap: 25px;
  gap: 25px;
}

@media screen and (max-width: 480px) {
  .txtFld {
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
  }
  .result {
    width: 80%;
  }
  .cnt2 {
    height: 100%;
  }
  .bt {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr;
    margin-top: 1em;
  }
}

